.card {
    padding: 5px;
    font-family: roboto;
    max-width: 300px;
    background-color: #fdfffc;
    border: 4px solid #011627;
  }
  
  .card-img-top {
    width: 300px;
    height: 18rem;
  }
  
  .card-brand {
    font-size: 0.6rem;
    align-self: flex-end;
  }
  
  .card-info {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  
  .card-color {
    width: 1rem;
    height: 1rem;
    border: 1px solid #011627;
    border-radius: 100%;
  }
  
  .card-text {
    height: 80px;
  }

  .card-price {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
    border-bottom: 2px dashed black;
  }