.form-card {
    padding: 5px;
    font-family: roboto;
    max-width: 250px;
    height: 250px;
    background-color: #fdfffc;
    border: 4px solid #011627;
}

.form-card-title {
    text-align: center;
    border-bottom: 1px dashed black;
    padding-bottom: 10px;
}

.form-card-text {
    padding: 0;
    margin: 10px;
}