.navbar {
  display: flex;
  gap: 2rem;
  align-items: center;
  justify-content: center;
  background-color: #aaaccc;
  height: 120px;
}

@media screen and (max-width: 600px) {
  .navbar {
    gap: 0;
    justify-content: space-between;

  }
  .navbar button {
    min-width: 50px;
    font-size: 18px;
  }
} 