.formContent {
    display: flex;
    justify-content: flex-start;
    gap: 100px;
    padding: 100px;
}

.formTitle {
    text-align: center;
}

@media screen and (max-width: 820px) {
    .formContent {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}